<template>
    <div>
        <h1 class="wrap-title mar-bottom15"></h1>
        <el-row :gutter="20">
            <el-col :span="8">
                <el-row class="mar-bottom10">
                    <el-col :span="24" class="el-item">
                        <el-card>
                            <div slot="header">
                                <span></span>
                            </div>
                            <el-form ref="form" :model="form" label-width="65px" class="pdd-10">
                                <el-form-item label="">
                                    <el-input v-model="form.b"></el-input>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-input v-model="form.b"></el-input>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-select v-model="form.a" placeholder="" filterable>
                                        <el-option label="smtp.163.com" :value="1"></el-option>
                                        <el-option label="smtp.126.com" :value="2"></el-option>
                                        <el-option label="smtp.qq.com" :value="3"></el-option>
                                        <el-option label="smtp.sina.com" :value="4"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-input v-model="form.b"></el-input>
                                </el-form-item>
                                <el-form-item label=" ">
                                    <el-checkbox v-model="form.is_secrecy">SSL</el-checkbox>
                                </el-form-item>
                                <el-form-item label=" ">
                                    <el-button type="primary" size="small"></el-button>
                                </el-form-item>
                            </el-form>
                        </el-card>
                    </el-col>
                </el-row>
            </el-col>
            <el-col :span="16">
                <el-row class="mar-bottom10">
                    <el-col :span="24" class="el-item">
                        <el-card>
                            <div slot="header">
                                <span></span>
                            </div>
                            <div class="">
                                <el-input v-model="a" placeholder="" class="mar-bottom20"></el-input>
                                <el-input v-model="a" placeholder="" class="mar-bottom20"></el-input>
                                <el-input v-model="a" placeholder="" type="textarea" :rows="8" class="mar-bottom20"></el-input>
                                <el-upload class="mar-bottom20" ref="upload" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="fileList" :auto-upload="false">
                                    <el-button slot="trigger" size="mini" type="warning"></el-button>
                                    <!-- <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button> -->
                                    <div slot="tip" class="el-upload__tip">32mb</div>
                                </el-upload>
                                <el-button type="primary" size="small"></el-button>
                            </div>
                        </el-card>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data () {
        return {
            a: '',
            form: {

            },
            formTest: {

            },
            fileList: []
        }
    },
    methods: {
        submitUpload() {
            this.$refs.upload.submit()
        },
        handleRemove(file, fileList) {
            console.log(file, fileList)
        },
        handlePreview(file) {
            console.log(file)
        }
    }
}
</script>



