        <el-form ref="form" :model="form" label-width="95px" class="content-form">
            <el-form-item label="">
                <el-input v-model="form.a"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="form.b" placeholder="" filterable>
                    <el-option label="" :value="0"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.c"></el-input>
            </el-form-item>
            <el-form-item label=" ">
                <el-button type="primary" size="small"></el-button>
                <el-button type="success" size="small"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
//  => 
export default {
    props: ['nowMeeting'],
    data() {
        return {
            list: [

            ],
            form: {
                a: '',
                b: '',
                c: '',
                d: '',
                e: '',
                f: ''
            }
        }
    }
}
</script>



